<template>
  <view class="msj-down" :style="downStyle">
    <div class="msj-down-header">
      <!-- Â§¥ÈÉ® -->
      <slot name="header" v-if="!fixedTop"></slot>
      <div
        :class="[
          'down',
          fixedTop ? 'is-fixed' : isFixedTopBall ? 'is-fixed' : ''
        ]"
      >
        <slot name="header" v-if="fixedTop"></slot>
        <!-- ËèúÂçï -->
        <div class="down-menu">
          <div
            class="down-menu-item"
            :class="[
              isGrow ? '' : 'each',
              border ? 'border' : '',
              item.class ? 'down-menu-' + item.class : '',
              getMenuList.length > 1 ? 'justify-center' : '',
              currentIndex === index ? 'is-actived' : ''
            ]"
            v-for="(item, index) in getMenuList"
            :key="index"
            @click="menuClick(item, index)"
          >
            <div class="down-menu-item-click" v-if="item.type === 'click'">
              <slot :name="item.slot" v-if="item.slot"> </slot>
              <template v-else>
                <text> {{ item.title }}</text>
                <slot v-if="item.slotIcon" :name="item.slotIcon"></slot>
                <i v-else-if="item.icon" :class="['click-icon', item.icon]"></i>
              </template>
            </div>
            <div
              v-else-if="item.type === 'sort'"
              class="down-menu-item-sort flex items-center"
            >
              <slot :name="item.slot" v-if="item.slot"> </slot>
              <template v-else>
                <text> {{ item.title }}</text>
                <div
                  class="sort-icon flex flex-col"
                  :class="[
                    item.value === 'asc'
                      ? 'is-actived-sort-icon-up'
                      : 'is-actived-sort-icon-down'
                  ]"
                >
                  <i class="iconfont up-fill" name="arrow-up-fill">
                    &#xeb6e;
                  </i>
                  <i class="iconfont down-fill" name="arrow-down-fill">
                    &#xeb6d;
                  </i>
                </div>
              </template>
            </div>
            <div class="down-menu-item-down" v-else-if="item.type === 'down'">
              <slot :name="item.slot" v-if="item.slot"> </slot>
              <template v-else>
                <text> {{ item.title }}</text>
                <i v-if="item.icon" :class="['down-icon', item.icon]"></i>
                <div v-else class="down-icon">
                  <i class="iconfont up-fill" v-if="currentIndex === index">
                    &#xeb6f;
                  </i>
                  <i class="iconfont" v-else> &#xeb70; </i>
                </div>
              </template>
            </div>
            <div
              class="down-menu-item-filter"
              v-else-if="item.type === 'filter'"
            >
              <slot :name="item.slot" v-if="item.slot"> </slot>
              <template v-else>
                <text> {{ item.title }}</text>
                <i v-if="item.icon" :class="['filter-icon', item.icon]"></i>
                <div v-else class="filter-icon">
                  <i class="iconfont up-fill" v-if="currentIndex === index">
                    &#xeb6f;
                  </i>
                  <i class="iconfont" v-else> &#xeb70; </i>
                </div>
              </template>
            </div>
          </div>
        </div>
        <!-- ÂºπÂá∫ -->
        <div
          class="down-ball"
          :class="{
            'is-show': isShow,
            'is-visible': isVisible,
            'is-show-ball': isFixedTopBall
          }"
        >
          <div class="down-ball-popup">
            <div
              class="down-ball-popup-content"
              :class="[
                menuItem.type === 'down' ? 'down-ball-popup-content-down' : ''
              ]"
            >
              <template v-for="(item, index) in getMenuList">
                <template v-if="item.type === 'down' && index === currentIndex">
                  <slot v-if="item.popupSlot" :name="popupName"></slot>
                  <down
                    v-else
                    :key="index"
                    :list="item"
                    :index="index"
                    @success="downSuccess"
                  />
                </template>
                <template
                  v-else-if="item.type === 'filter' && index === currentIndex"
                >
                  <slot v-if="item.popupSlot" :name="popupName"></slot>
                  <msj-filter
                    v-else
                    :key="index"
                    :list="item"
                    :index="index"
                    @changeItem="changeItem"
                  >
                  </msj-filter>
                </template>
              </template>
            </div>
            <template v-if="menuItem.type !== 'down'">
              <div
                class="down-ball-popup-btn flex justify-between"
                v-if="isBtn"
              >
                <button
                  class="flex-1 resetting"
                  :hair-line="false"
                  ripple
                  @click="handleResetting('resetMenu', true)"
                >
                  Èáç ÁΩÆ
                </button>
                <button
                  class="flex-1 confirm"
                  type="primary"
                  ripple
                  @click="confirm"
                >
                  Á°Æ ÂÆö
                </button>
              </div>
            </template>
          </div>
          <div class="down-ball-mask" @tap="handlePopupHide"></div>
        </div>
      </div>
    </div>

    <!-- ÂÜÖÂÆπ -->
    <div class="msj-down-content" :class="fixedTop ? 'margin' : ''">
      <slot></slot>
    </div>
  </view>
</template>
<script>
import { deepClone } from './utils'
import down from './components/down'
import MsjFilter from './components/filter'
export default {
  name: 'msj-down',
  components: { down, MsjFilter },
  mixins: [],
  props: {
    /**
     * ÂØºËà™ËèúÂçïÊï∞ÊçÆ
     */
    menuList: {
      type: Array,
      default: () => []
    },
    /**
     * ÊøÄÊ¥ªÈ¢úËâ≤
     */
    themeColor: {
      type: String,
      default: '#007aff'
    },
    /**
     * Â∏∏ËßÑÈ¢úËâ≤
     */
    textColor: {
      type: String,
      default: '#000000'
    },
    /**
     * ÊòØÂê¶Âõ∫ÂÆöÂú®È°∂ÈÉ®
     */
    fixedTop: {
      type: Boolean,
      default: false
    },
    /**
     * ÂºπÁ™óËøáÊ∏°Êó∂Èó¥
     */
    duration: {
      type: [Number, String],
      default: 300
    },
    /**
     * È´òÂ∫¶
     */
    height: {
      type: [Number, String],
      default: 80
    },
    /**
     * ÊòØÂê¶ÊòæÁ§∫ËæπÊ°Ü
     */
    border: {
      type: Boolean,
      default: false
    },
    /**
     * ÊòØÂê¶Ëá™ÂÆö‰πâÊ†∑Âºè
     */
    isGrow: {
      type: Boolean,
      default: false
    },
    /**
     * ÊòØÂê¶ÊòæÁ§∫Á°ÆËÆ§ ÈáçÁΩÆ ÊåâÈíÆ
     */
    isBtn: {
      type: Boolean,
      default: true
    },
    /**
     * Â≠ó‰ΩìÂ§ßÂ∞è
     */
    fontSize: {
      type: String,
      default: '14'
    }
  },
  filters: {},
  data() {
    return {
      createMargin: null,
      currentIndex: -1,
      popupName: '',
      isShow: false,
      isVisible: false,
      menuItem: {},
      isFixedTopBall: false, // ÂºπÊ°ÜÂºπÂá∫Âêé Âõ∫ÂÆö
      marginFix: null,
      resetMenu: null, // ÈáçÁΩÆÊï∞ÁªÑ copy
      newMenuCopy: null, //‰øÆÊîπÂêéÁöÑÊï∞ÊçÆ Â§á‰ªΩ
      isNewMenu: false // Âà§Êñ≠ÊòØÂê¶ ‰øÆÊîπÊàê ‰∏ä‰∏ÄÊ¨°ÁöÑÊï∞ÊçÆ ÂçïÈ°π‰øÆÊîπ
    }
  },
  computed: {
    getMenuList() {
      const allItem = {
        label: 'ÂÖ®ÈÉ®',
        value: 'all'
      }
      const newMenu = deepClone(this.menuList || [])
      // click ÁÇπÂáª Âè™ÈúÄË¶ÅÁÇπÂáª
      // Êï∞ÊçÆ
      // {
      //   title: 'Á±ªÂûãÁ≠õÈÄâ', // Ê†áÈ¢ò
      //   type: 'click', // Á±ªÂûã
      // }

      // sort ÊéíÂ∫è
      // value : asc desc

      newMenu.forEach(item => {
        if (item.type === 'sort') {
          if (!item.value) {
            item.value = 'asc'
          }
        } else if (item.type === 'down') {
          // ÊòØÂê¶ÊòØÊèíÊßΩ ÈªòËÆ§Ê≤°ÊúâÊèíÊßΩ
          if (!item.popupSlot) {
            if (!item.value) {
              item.value = 'all'
            }
            // Ê≤°ÊúâÊèíÊßΩÁöÑÊÉÖÂÜµ ÁúãÊòØÂê¶Êúâoptions
            if (!item.options) {
              item.options = []
            }
            if (item.isAll) {
              item.options.unshift(allItem)
            }
          }
        } else if (item.type === 'filter') {
          // ÊòØÂê¶ÊòØÊèíÊßΩ ÈªòËÆ§Ê≤°ÊúâÊèíÊßΩ
          if (!item.popupSlot) {
            if (!item.value) {
              item.value = ['all']
            }
            // Ê≤°ÊúâÊèíÊßΩÁöÑÊÉÖÂÜµ ÁúãÊòØÂê¶Êúâoptions
            if (!item.options) {
              item.options = []
            }
            item.options.forEach(filterItem => {
              if (filterItem.isAll) {
                filterItem.options.unshift(allItem)
              }
            })
          }
        }
        // console.log(el, 'el')
      })
      this.resetMenu = JSON.parse(JSON.stringify(newMenu))
      this.newMenuCopy = JSON.parse(JSON.stringify(newMenu))
      return newMenu
    },
    /**
     * Ê†∑Âºè
     */
    downStyle() {
      this.marginTopHanld()
      return `
        --down-theme-color: ${this.themeColor};
        --down-text-color: ${this.textColor};
        --down-popup-duration: ${this.duration / 1000}s;
        --down-menu-height: ${this.height / 2 || 0}px;
        --down-margin-top: ${this.createMargin}px;
        --down-margin-top-fix: ${this.marginFix}px;
        --down-font-size: ${this.fontSize}px;
      `
    }
  },
  watch: {},
  mounted() {},
  created() {},
  methods: {
    /**
     * Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØÈ´òÂ∫¶
     */
    marginTopHanld() {
      return this.$nextTick(() => {
        const select = uni.createSelectorQuery().in(this)
        select
          .select('.msj-down .down')
          .boundingClientRect(data => {
            // Âú®ËøôÈáåÂèØ‰ª•Ëé∑ÂèñÂà∞ÂÖÉÁ¥†ÁöÑ‰ø°ÊÅØÔºå‰æãÂ¶ÇÈ´òÂ∫¶„ÄÅÂÆΩÂ∫¶Á≠â
            // console.log('ÂÖÉÁ¥†ÁöÑÈ´òÂ∫¶Ôºö', data)
            if (data) {
              // Âú®ËøôÈáåÂèØ‰ª•Ëé∑ÂèñÂà∞ÂÖÉÁ¥†ÁöÑÈ´òÂ∫¶
              this.marginFix = data.height
            }
          })
          .exec()
        if (this.fixedTop) {
          select
            .select('.msj-down .down')
            .boundingClientRect(data => {
              // Âú®ËøôÈáåÂèØ‰ª•Ëé∑ÂèñÂà∞ÂÖÉÁ¥†ÁöÑ‰ø°ÊÅØÔºå‰æãÂ¶ÇÈ´òÂ∫¶„ÄÅÂÆΩÂ∫¶Á≠â
              // console.log('ÂÖÉÁ¥†ÁöÑÈ´òÂ∫¶Ôºö', data)
              if (data) {
                // Âú®ËøôÈáåÂèØ‰ª•Ëé∑ÂèñÂà∞ÂÖÉÁ¥†ÁöÑÈ´òÂ∫¶
                this.createMargin = data.height
              }
            })
            .exec()
        } else {
          select
            .select('.msj-down .msj-down-header')
            .boundingClientRect(data => {
              // Âú®ËøôÈáåÂèØ‰ª•Ëé∑ÂèñÂà∞ÂÖÉÁ¥†ÁöÑ‰ø°ÊÅØÔºå‰æãÂ¶ÇÈ´òÂ∫¶„ÄÅÂÆΩÂ∫¶Á≠â
              // console.log('ÂÖÉÁ¥†ÁöÑÈ´òÂ∫¶Ôºö', data)
              if (data) {
                // Âú®ËøôÈáåÂèØ‰ª•Ëé∑ÂèñÂà∞ÂÖÉÁ¥†ÁöÑÈ´òÂ∫¶
                this.createMargin = data.height
              }
            })
            .exec()
        }

        return this.createMargin || 0
      })
    },
    /**
     * ÁÇπÂáªËèúÂçï
     */
    menuClick(item, index) {
      this.menuItem = item
      // ÁÇπÂáª
      if (item.type === 'click') {
        this.handlePopupHide(1)
        this.$emit(item.emit || 'click', item, index)
        return true
      } else if (item.type === 'sort') {
        // ÊéíÂ∫è
        this.handlePopupHide(1)
        item.value = item.value === 'asc' ? 'desc' : 'asc'
        this.$forceUpdate()
        this.$emit(item.emit || 'sort', item, index)
        return true
      } else if (item.type === 'down') {
        // console.log(item, 'item')
        // ‰∏ãÊãâ
        if (item.popupSlot) {
          this.popupName = item.popupSlot
          this.$emit(item.emit || 'down', item, index)
        } else {
          this.popupName = 'popupDown'
          this.$emit(item.emit || 'down', item, index)
        }
        // console.log(this.popupName)
        // $forceUpdate()
      } else if (item.type === 'filter') {
        if (item.popupSlot) {
          this.popupName = item.popupSlot
          this.$emit(item.emit || item.type, item, index)
        } else {
          this.popupName = 'popupFilter'
          this.$emit(item.emit || item.type, item, index)
        }
        if (!item.emit) {
          item.emit = item.type
        }
      }
      this.handlePopupShow(index)
      // console.log(2222, item)
    },

    /**
     * Á°ÆÂÆö
     */
    confirm() {
      this.newMenuCopy[this.currentIndex] = JSON.parse(
        JSON.stringify(this.getMenuList[this.currentIndex])
      )
      this.isNewMenu = true // ‰∏çÈáçÁΩÆ
      this.handlePopupHide()

      this.$emit('confirm', this.menuItem, this.currentIndex)
      if (this.menuItem.type === 'filter') {
        let data = {}
        this.menuItem.options.forEach(item => {
          data[item.type] = []
        })

        console.log('üöÄ ~ file: msj-down.vue:453 ~ confirm ~ data:', data)
        this.$emit(this.menuItem.emit, this.menuItem, this.currentIndex)
      }
    },

    /**
     * ÈáçÁΩÆ
     */
    handleResetting(name, isReset) {
      // console.log(name, isReset)
      this.getMenuList[this.currentIndex] = JSON.parse(
        JSON.stringify(this[name][this.currentIndex])
      )
      if (!!isReset) {
        this.newMenuCopy[this.currentIndex] = JSON.parse(
          JSON.stringify(this.getMenuList[this.currentIndex])
        )
        this.$emit('resetting')
      }
      this.$forceUpdate()
    },
    /**
     * ÊâìÂºÄÂºπÁ™ó
     * @param index ÂΩìÂâçÊøÄÊ¥ªÁ¥¢Âºï
     */
    handlePopupShow(index) {
      this.isShow = true
      this.isVisible = true
      this.currentIndex = index
      this.isFixedTopBall = true
      this.$emit('open', this.currentIndex)
    },
    /**
     * ÂÖ≥Èó≠ÂºπÁ™ó
     */
    handlePopupHide(duration) {
      this.isShow = false
      this.isFixedTopBall = false

      // Âª∂ËøüÁßªÈô§‰∏ãÊãâÂºπÁ™ó
      setTimeout(
        () => {
          if (!this.isNewMenu) {
            this.handleResetting('newMenuCopy', false)
          }
          this.currentIndex = -1
          this.isVisible = false
          this.isNewMenu = false
          // this.getMenuList.forEach(item => {
          //   item.isActived = false
          // })
        },
        typeof duration === 'number' ? duration : this.duration
      )
      this.$emit('close', this.currentIndex)
    },
    /**
     * ËèúÂçïÈ°π-‰∏ãÊãâÂàóË°®ÂõûË∞É
     * @param value Êìç‰ΩúËøîÂõûÁöÑÊï∞ÊçÆ
     * @param item ‰∏ãÊãâÂàóË°®È°πÊï∞ÊçÆ
     * @param itemIndex ‰∏ãÊãâÂàóË°®È°πÁ¥¢Âºï
     * @param index ËèúÂçïÁ¥¢Âºï
     */
    downSuccess(value, item, itemIndex, index) {
      let itemList = this.getMenuList[index]
      itemList.value = value // Êõ¥ÊîπÂÄº
      this.newMenuCopy[index] = this.getMenuList[index]
      this.isNewMenu = true // ‰∏çÈáçÁΩÆ
      this.$forceUpdate()
      this.$emit(
        item.emitConfim || 'downConfirm',
        value,
        item,
        itemIndex,
        index
      )
      this.handlePopupHide()
    },
    /**
     *
     * @param {*} item ÂΩìÂâçÈ°π
     * @param {*} index Á¥¢Âºï
     * @param {*} menuIndex Á±ªÂûãÁ¥¢Âºï
     */
    changeItem(item, index, menuIndex) {
      console.log(
        'üöÄ ~ file: msj-down.vue:523 ~ changeItem ~ item, index, menuIndex:',
        item,
        index,
        menuIndex
      )
      this.getMenuList[menuIndex].options[index] = item
    }
  }
}
</script>
<style lang='scss' scoped>
@import './common.scss';

.msj-down {
  --icon-mr-left: 10rpx;
  position: relative;
  z-index: 900;
  width: 100%;
  background-color: #fff;
  .down {
    &.is-fixed {
      position: fixed;
      top: var(--window-top, 0px);
      right: 0;
      left: 0;
      z-index: 910;
      display: block;
      height: auto;
      background-color: #fff;
    }

    &-menu {
      position: relative;
      z-index: 920;
      height: var(--down-menu-height, 0px);
      display: flex;
      align-items: center;
      box-shadow: 0 1rpx 0 0 #ececec;
      color: var(--down-text-color);
      &-item {
        display: flex;
        align-items: center;
        height: 100%;
        padding: 0 20rpx;
        &.is-actived {
          color: var(--down-theme-color);
        }
        &.each {
          flex-grow: 1;
        }
        &.border {
          border-right: 1px solid #ececec;
        }
        &-click,
        &-down,
        &-filter {
          display: flex;
          align-items: center;
          // width: 100%;
          // height: 100%;
          font-size: var(--down-font-size);
          .click-icon,
          .down-icon,
          .filter-icon {
            margin-left: var(--icon-mr-left);
          }
        }
        &-sort {
          font-size: var(--down-font-size);
          .sort-icon {
            margin-left: var(--icon-mr-left);
            u-icon {
              display: flex;
            }
          }
          .is-actived-sort-icon {
            &-up,
            &-down {
              .up-fill,
              .down-fill {
                color: $tips-color;
              }
            }
            &-up {
              .up-fill {
                color: var(--down-theme-color);
              }
            }
            &-down {
              .down-fill {
                color: var(--down-theme-color);
              }
            }
          }
        }
        &-down {
          .down-icon {
            margin-left: var(--icon-mr-left);
            u-icon {
              display: flex;
              color: $tips-color;
            }
            .up-fill {
              color: var(--down-theme-color);
            }
          }
        }
      }
    }

    &-ball {
      position: absolute;
      top: var(--down-margin-top);
      bottom: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      z-index: 930;
      overflow: hidden;
      // opacity: 0;
      visibility: hidden;
      height: calc(100vh - var(--down-menu-height, 0px));
      z-index: 901;

      &.is-show {
        .down-ball-popup {
          transform: translateY(0);
        }
      }

      &.is-show-ball {
        top: var(--down-margin-top-fix);
      }
      &.is-visible {
        // opacity: 1;
        visibility: visible;
      }
      &-popup {
        position: relative;
        z-index: 10;
        max-height: 100%;
        overflow: auto;
        transform: translateY(-120%);
        transition: transform var(--down-popup-duration) linear,
          -webkit-transform var(--down-popup-duration) linear;
        &-content {
          padding: 20rpx;
          background-color: #fff;
          &-down {
            padding: 0;
          }
        }
        &-btn {
          background-color: #fff;
          padding: 20rpx 20rpx;
          // :deep(button) {
          //   // border-radius: 5px;
          // }
          .resetting,
          .confirm {
            margin-right: 30rpx;
            padding: 14rpx 0;
            line-height: inherit;
            border-radius: 35rpx;
          }
          .confirm {
            color: #fff;
            background-color: var(--down-theme-color);
          }

          .resetting {
            background-color: #ffffff;
            border: 1px solid var(--down-theme-color);
            color: var(--down-theme-color);
          }
        }
      }

      &-mask {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        z-index: 9;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.6);
      }
    }
  }
  &-content {
    &.margin {
      margin-top: calc(var(--down-margin-top));
    }
  }
}
</style>
